import React from 'react';
import "./Gallery.css"
import {colorScheme} from "../../ColorScheme.js"
import './SlideModal.css';
import CloseIcon from '../../Images/CloseIcon.png'

export default class Card extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      imageWidth: '100vw',
      imageHeight: '50vh',
      width: window.innerWidth,
      height: window.innerHeight,
      inView: false,
      hold:false
    };
}

componentDidMount() {
 }

 componentWillUnmount() {
 }

closeModal = () => {
  let modal = document.querySelector('.SlideModal')
  modal.style.animation = 'closeSlideModal 0.5s'
  modal.style.animationFillMode = 'forwards'
}

render() {

    return (
        <div className="SlideModal" 
        onClick={()=>this.closeModal()}
        >
        <div className="SlideModalChild">
            <span>We will save this image for you using </span>
            <a
            style={{color:colorScheme.fourth}}
            href="https://www.dummies.com/education/internet-basics/defining-and-dealing-with-web-cookies/" target="_blank" rel="noopener noreferrer"
            >cookies</a>
            . Cookies allow us to store your customizations and improve your experience. If you do not want to use cookies, just unsave the image
        </div>
        <img className="Close-Image" src={CloseIcon} alt='placeholder' width="40" height="40"/>
        </div>
    );
  }


}
